.go-genesis {
  height: auto;
  padding: 0px;
  color: #fff;

  .header-backdrop {
    width: 100%;
    padding: 5rem 0;
    z-index: 0;
    justify-content: space-around;
    background-image: linear-gradient(to bottom, #ff4988, #ff8cb2);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center center;
  }

  .border-list {
    border-left: 4px solid #ff6c6c;
  }

  .button-link {
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    color: #fff;
    border: 1px solid #fff;

    &:hover {
      color: white;
    }
  }
  .button-link::after {
    background-image: linear-gradient(to right, #ff4988, #ff8cb2);
  }

  .animate-text-hover {
    min-width: 10px;
    display: inline-block;
    animation-fill-mode: both;

    &:hover {
      animation: rubberBand 1s;
      color: #000000;
    }
  }

  .wrapper {
    width: 100%;
    display: flex;
    margin: 2.4rem 0;
    flex-direction: row;
    align-items: center;
    justify-content: space-around;

    @media screen and (max-width: 800px) {
      width: 100%;
      text-align: left;
      font-size: 1.4rem;
      flex-direction: row;
    }

    img {
      width: 600px;
    }

    .zoomedimg {
      width: 400px;
      margin: auto;
    }

    p.wrapper-text {
      display: flex;
      padding: 0 2rem;
      flex-direction: column;
      align-items: center;
      text-align: center;
    }
  }

  .header-zone {
    width: 100%;
    margin: 0 0 1rem 0;
    text-align: center;

    h1 {
      color: #fff;
      margin-bottom: 2px;
    }
    h2 {
      color: #fff;
      font-size: 2.2rem;
      letter-spacing: 0px;
    }
  }
}
